<template>
<div class="swiper-container" :class="swipeid">
	<div class="swiper-wrapper">
		<slot name="swiper-con"></slot>
	</div>
	<!--  -->
	<div :class="{'swiper-pagination':pagination}"></div>
</div>
</template>
<script>
import './swiper.js'
export default {
	name: "m-swipe",
	props: {
		swipeid: {
			type: String,
			default: ''
		},
		effect: {
			type: String,
			default: 'slide'
		},
		loop: {
			type: Boolean,
			default: false
		},
		direction: {
			type: String,
			default: 'horizontal'
		},
		pagination: {
			type: Boolean,
			default: true
		},
		autoplay: {
			type: Number,
			default: 5000,
		},
		paginationType: {
			type: String,
			default: 'bullets'
		}
	},
	data(){
		return {
			dom:""
		}
	},
	mounted() {
		var That = this;
		this.dom = new Swiper('.' + That.swipeid, {
			//
			loop: That.loop,
			//
			pagination: '.swiper-pagination',
			//
			paginationType: That.paginationType, //fraction,progress,bullets
			//
			// autoplay: That.autoplay,
			autoplay: That.autoplay,
			//
			direction: That.direction,
			//
			effect: That.effect, //slide,fade,coverflow,cube
			autoplayDisableOnInteraction: false,
			observer: true, //swiperswiper
			observeParents: true //swiperswiper
		})
	}
}
</script>
<style>
@import './swiper.min.css';
.swiper-pagination-bullet-active {
	background: #fff;
}
</style>



